import { Component, EventEmitter, forwardRef, Input, Output, ViewEncapsulation } from '@angular/core';
import { NgClass } from '@angular/common';
import { A11yModule } from '@angular/cdk/a11y';
import { MatFormFieldModule } from '@angular/material/form-field';
import {
  AbstractControl,
  FormsModule,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ReactiveFormsModule,
  ValidationErrors,
  Validator
} from '@angular/forms';
import { MatInputModule } from '@angular/material/input';
import { MatRadioModule } from '@angular/material/radio';
import { MatCheckboxChange, MatCheckboxModule } from '@angular/material/checkbox';
import { BooleanInput, coerceBooleanProperty } from '@angular/cdk/coercion';

import { BaseControlValueAccessor } from 'sidsc-components/core';

export type DscCheckboxLabelPosition = 'before' | 'after';

export type DscCheckboxVariant = 'highlight' | 'accent' | 'danger';

export type DscCheckboxSize = 'large' | 'standard' | 'small';

@Component({
  selector: 'dsc-checkbox',
  standalone: true,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    MatInputModule,
    MatFormFieldModule,
    MatRadioModule,
    MatCheckboxModule,
    NgClass,
    A11yModule
  ],
  templateUrl: './dsc-checkbox.component.html',
  styleUrls: ['./dsc-checkbox.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DscCheckboxComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: DscCheckboxComponent,
      multi: true
    }
  ]
})
export class DscCheckboxComponent extends BaseControlValueAccessor<BooleanInput> implements Validator {

  @Input() labelPosition: DscCheckboxLabelPosition = 'after';

  @Input() name!: string;

  @Input() label?: string;

  @Input('aria-describedby') ariaDescribedby!: string;

  @Input('aria-label') ariaLabel!: string;

  @Input('aria-labelledby') ariaLabelledby!: string | null;

  @Input()
  get indeterminate(): boolean {
    return this._indeterminate;
  }
  set indeterminate(value: BooleanInput) {
    this._indeterminate = coerceBooleanProperty(value);
  }

  private _indeterminate: boolean = false;

  @Input()
  get checked(): boolean {
    return this._checked;
  }
  set checked(value: BooleanInput) {
    this._checked = coerceBooleanProperty(value);
  }

  private _checked: boolean = false;

  @Input()
  get required(): boolean {
    return this._required;
  }
  set required(value: BooleanInput) {
    this._required = coerceBooleanProperty(value);
  }

  private _required: boolean = false;

  @Input()
  get size(): DscCheckboxSize {
    return this._size;
  }

  set size(value: DscCheckboxSize) {
    this._size = value;
    this.checkboxLabelSize = `mat-mdc-checkbox--${value}`;
  }

  private _size: DscCheckboxSize = 'standard';

  protected checkboxLabelSize = 'mat-mdc-checkbox--standard';

  @Input()
  get variant(): DscCheckboxVariant {
    return this._variant;
  }

  set variant(value: DscCheckboxVariant) {
    this._variant = value;
    this.checkboxVariant = `mat-mdc-checkbox--${value}`;
  }

  private _variant: DscCheckboxVariant = 'highlight';

  protected checkboxVariant = 'mat-mdc-checkbox--highlight';

  @Output() readonly change: EventEmitter<any> = new EventEmitter<any>();

  @Output() readonly indeterminateChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  onChanged($event: MatCheckboxChange): void {
    this.change.emit($event);
  }

  onIndeterminateChange(value: boolean): void {
    this.indeterminateChange.emit(value);
  }

  toggle(): void {
    this.checked = !this.checked;
    this.val = this.checked;
  }

  override writeValue(value: BooleanInput): void {
    this.checked = !!value;
    this.val = this.checked;
  }

  validate(control: AbstractControl<any, any>): ValidationErrors | null {
    return control.value === null || control.value === undefined ? { required: true } : null;
  }

  @Input()
  override get disabled(): boolean {
    return this._disable;
  }

  override set disabled(value: BooleanInput) {
    this._disable = coerceBooleanProperty(value);
  }

  private _disable: boolean = false;
}
